import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { MapPin } from "lucide-react"

export function LogisticsMap() {
  return (
    <Card className="col-span-1 lg:col-span-4 h-[400px] bg-slate-950 relative overflow-hidden border-border">
      <CardHeader className="absolute top-0 left-0 z-10 w-full bg-background/80 backdrop-blur-sm border-b p-4">
        <CardTitle className="text-sm font-medium flex items-center gap-2">
            <MapPin className="h-4 w-4 text-primary" />
            Live Shipment Tracking
        </CardTitle>
      </CardHeader>
      <div className="w-full h-full flex items-center justify-center text-muted-foreground bg-[url('/map-placeholder.png')] bg-cover bg-center">
        {/* Placeholder for Map - In real app, use Leaflet or Google Maps */}
        <div className="text-center p-8 bg-background/90 rounded-lg border backdrop-blur">
            <MapPin className="h-12 w-12 text-primary mx-auto mb-4 animate-bounce" />
            <p>Interactive Map Module</p>
            <p className="text-xs text-muted-foreground">Waiting for IoT Signal...</p>
        </div>
      </div>
    </Card>
  )
}
