import { Sidebar } from "./Sidebar"
import { MobileMenu } from "./MobileMenu"
import { IntelligencePanel } from "@/components/dashboard/IntelligencePanel"
import { IntelligenceDrawer } from "@/components/dashboard/IntelligenceDrawer"

interface DashboardLayoutProps {
  children: React.ReactNode
}

export function DashboardLayout({ children }: DashboardLayoutProps) {
  return (
    <div className="flex h-screen bg-background text-foreground font-sans antialiased overflow-hidden">
      {/* Desktop Sidebar */}
      <Sidebar className="hidden w-64 lg:block shrink-0 overflow-y-auto" />

      {/* Main Content Area */}
      <div className="flex-1 flex flex-col min-w-0 h-full">
        {/* Mobile Header */}
        <header className="sticky top-0 z-40 flex items-center h-16 px-4 border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 lg:hidden shrink-0">
          <MobileMenu />
          <h1 className="ml-4 text-lg font-bold">AgenticSCM</h1>
        </header>

        {/* Page Content */}
        <main className="flex-1 overflow-y-auto p-4 md:p-6 lg:p-8">
            {children}
        </main>
      </div>

      {/* Desktop Intelligence Panel */}
      <IntelligencePanel className="hidden w-80 lg:flex shrink-0 border-l overflow-y-auto" />

      {/* Mobile Intelligence Drawer */}
      <IntelligenceDrawer />
    </div>
  )
}
